import React from 'react';
import ReactHoverObserver from 'react-hover-observer';

import Box from '../../components/Box';
// import Text from '../components/Text';
import Flex from '../../components/Flex';
import Link from '../../components/Link';
// import BackgroundImage from '../components/BackgroundImage';
import Circle from '../../components/Circle';

import Email from './Email';
import Relabmobile from './Relabmobile';
import Home from './Home';

const contacts = [
  {
    Img: Relabmobile,
    text: '',
    to: '/about',
  },
  {
    Img: Email,
    text: '',
    to: '/contact',
  },
  {
    Img: Home,
    text: '',
    to: '/',
  },
];

export default ({ blue, ...props }) => {
  const main = blue ?'blue' : 'white';
  const sub = blue ?'white' : 'blue';
  return (
    <Box position="relative" {...props}>
      <Flex>
        {contacts.map(({ to, Img, text }, index) => (
          <Box key={index}>
            <ReactHoverObserver>
              {({ isHovering }) => (
                <Box position="relative">
                  <Box w="2em" mx="0.25em">
                    <Link
                      to={to}
                      px="0"
                      py="0"
                    >
                      <Circle bg={isHovering ? sub : main}>
                        <Img viewBox="0 0 53.51 53.51" w={1} color={isHovering ? main : sub} />
                      </Circle>
                    </Link>
                  </Box>
                  <Box
                    position="absolute"
                    f="0.75em"
                    color={isHovering ? sub : main}
                    mt="1em"
                    left="50%"
                    transform="translateX(-50%)"
                    zIndex={Number(isHovering)}
                    whiteSpace="nowrap"
                  >{text}</Box>
                </Box>
              )}
            </ReactHoverObserver>
          </Box>
        ))}
      </Flex>
    </Box>
  );
}




