import React from 'react';

import Svg from '../../components/Svg';


const Email = (props) => (
  <Svg viewBox="0 0 60.25 60.25" {...props}>
    <g>
    <circle cx="26.76" cy="26.76" r="26.26" fill="none" stroke="currentColor" stroke-miterlimit="10"/>
    <g>
      <rect x="13.7" y="17.38" width="26.1" height="18.75" fill="none" stroke="currentColor" stroke-miterlimit="10"/>
      <polyline points="13.71 17.38 26.76 26.76 39.81 17.38" fill="none" stroke="currentColor" stroke-miterlimit="10"/>
    </g>
  </g>
  </Svg>
)

export default Email;



