import React from 'react';

import Svg from '../../components/Svg';


const Email = (props) => (
  <Svg viewBox="0 0 60.25 60.25" {...props}>
    <g>
      <circle cx="26.76" cy="26.76" r="26.26" fill="none" stroke="currentColor" stroke-miterlimit="10"/>
      <g>
        <polygon points="39.81 39.54 13.71 39.54 13.71 20.58 26.76 11.97 39.81 20.58 39.81 39.54" fill="none" stroke="currentColor" stroke-miterlimit="10"/>
        <rect x="23.09" y="25.25" width="7.34" height="14.29" fill="none" stroke="currentColor" stroke-miterlimit="10"/>
      </g>
    </g>
  </Svg>
)

export default Email;



