import merge from 'lodash/merge';
import range from 'lodash/range';

import constants from 'styled-system/dist/constants';

const emToPx = (em) => em * 16;

export const breakpoints = [36, 48, 62, 90, 120].map(emToPx);
export const containerWidth = [36, 46, 60, 70].map(emToPx);

const generateFade = (r, g, b) => range(10, 100, 10)
  .reduce((fade, opacity) => merge(fade, { [opacity]: `rgba(${[r, g, b, opacity / 100].join()})` }), {});

const font = 'Montserrat, "PingFang TC", "HeiTi TC", "Microsoft JhengHei", sans-serif';

const white = '#fff';
const black = '#000';
const gray = '#666666';
const lightGray='#F7F7F7';
const blue = '#0092D3';
const lightBlue = '#8ACAFF';
const yellow = '#FFDD15';
const red = '#f15a24';

export default merge(constants, {
  colors: {
    white,
    black,
    gray,
    blue,
    lightBlue,
    yellow,
    lightGray,
    red,
    text: gray,
    fade: {
      white: generateFade(255, 255, 255),
      black: generateFade(0, 0, 0),
    },
  },
  breakpoints,
  containerWidth,
  font,
  duration: 250,
  headerHeight: '3.5em',
});



